            bundleId: Yup.string().matches(/^[a-z][a-z0-9_]*(\.[a-z0-9_]+)+[0-9a-z_]$/i, i18n.t("YupInvalidBundleId")).required(i18n.t("YupValueRequired")),
            //versionNumber: Yup.string().required(i18n.t("YupValueRequired")),
